a,
a:focus,
a:hover {
  color: #fff;
}

html,
body {
  background-color: #333;
}

body {
  display: flex;

  width: 100%;
  min-height: 100vh;

  box-shadow: inset 0 0 5rem rgba(0, 0, 0, .5);
}

.container {
  max-width: 42em;
}

#start-box .search-input:disabled {
  background-color: #aaa;
}

#start-box fieldset:disabled .non-selected-wrapper {
  background-color: #888;
}

#start-box fieldset:disabled .selected-wrapper {
  background-color: #bbb;
}

.show-on-disabled {
  display: none;
}

fieldset:disabled .show-on-disabled {
  display: block;
}

#loading-box {
  display: none;
}

#activity-select-box {
  display: none;
}

.activity-select-dropdown {
  background-color: #007bff;
}
.activity-select-dropdown:hover {
  background-color: #0055b4;
}
.activity-select-dropdown:active {
  background-color: #003776;
}

.activity-select-delete {
  background-color: #dc3545;
}
.activity-select-delete:hover {
  background-color: #93242e;
}
.activity-select-delete:active {
  background-color: #521019;
}
